import uniDrawer from '@/components/uni-drawer/uni-drawer.vue';
export default {
	components: {
		uniDrawer
	},
	data() {
		return {
			showRigth: false,
			showLeft: false,
			head: ['综合', '销量', '价格'],

			datas: [
				{
					name: '真无线蓝牙耳机',
					txt: '雅致简约/分体式入耳/收纳盒充电/蓝牙5.0、触控操作',
					rmb: '199',
					pinglun: '1348'
				}
			]
		};
	},
	methods: {
		tiao(){
			uni.navigateTo({
				url:"../../../components/home/xqing"
			})
		},
		res() {
			//关闭筛选
			console.log('取消');
			// this.showRigth = false;
		},
		show(e) {
			//显示筛选
			this.showRigth = true;
		},
		hide() {
			//关闭筛选
			this.showRigth = false;
		},
		closeDrawer(e) {
			this.showRigth = false;
		},
		confirm() { }
	},

	onNavigationBarButtonTap(e) {
		uni.navigateTo({
			url: '../search/search'
		});
	}
};
